
'use client';

import { useEffect } from 'react';

export function usePageBackground(imageUrl: string) {
  useEffect(() => {
    if (typeof window !== 'undefined') {
        document.body.style.backgroundImage = `url('${imageUrl}')`;
    }

    return () => {
      if (typeof window !== 'undefined') {
        document.body.style.backgroundImage = '';
      }
    };
  }, [imageUrl]);
}
